"use client"

import { useState, useEffect, lazy, Suspense } from "react"
import { HeroCarousel } from "@/components/hero-carousel"
import { Navbar } from "@/components/navbar"
import { HeroContent } from "@/components/hero-content"
import { ServicesMarquee } from "@/components/services-marquee"
import { ThemeToggle } from "@/components/theme-toggle"
import PageLoader from "@/components/page-loader"

// Lazy load heavy components
const PhilosophySection = lazy(() => import("@/components/philosophy-section").then(mod => ({ default: mod.PhilosophySection })))
const ServicesSection = lazy(() => import("@/components/services-section").then(mod => ({ default: mod.ServicesSection })))
const ProcessSection = lazy(() => import("@/components/process-section"))
const TestimonialsSection = lazy(() => import("@/components/testimonials-section"))
const KineticSection = lazy(() => import("@/components/kinetic-section"))
const BookingSection = lazy(() => import("@/components/booking-section"))
const Footer = lazy(() => import("@/components/footer"))
const BackToTop = lazy(() => import("@/components/back-to-top"))

export default function HomePage() {
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    // Ensure minimum loading time for smooth experience
    const minLoadTime = setTimeout(() => {
      setIsLoading(false)
    }, 3500)

    return () => clearTimeout(minLoadTime)
  }, [])

  return (
    <>
      {isLoading && <PageLoader onLoadComplete={() => setIsLoading(false)} />}
      <main className="relative overflow-hidden">
      {/* Hero Section with Background Carousel */}
      <div id="home" className="relative min-h-screen">
        <HeroCarousel />
        <Navbar />
        <ThemeToggle />
        <HeroContent />
        <ServicesMarquee />
      </div>

      {/* Philosophy Section */}
      <Suspense fallback={<div className="min-h-screen" />}>
        <div id="philosophy">
          <PhilosophySection />
        </div>
      </Suspense>

      {/* Services Section */}
      <Suspense fallback={<div className="min-h-screen" />}>
        <div id="services">
          <ServicesSection />
        </div>
      </Suspense>

      {/* Process Timeline Section */}
      <Suspense fallback={<div className="min-h-screen" />}>
        <div id="process">
          <ProcessSection />
        </div>
      </Suspense>

      {/* Testimonials Section */}
      <Suspense fallback={<div className="min-h-screen" />}>
        <div id="testimonials">
          <TestimonialsSection />
        </div>
      </Suspense>

      {/* Kinetic Typography Experience Section */}
      <Suspense fallback={<div className="min-h-screen" />}>
        <KineticSection />
      </Suspense>

      {/* Booking Call-to-Action Section */}
      <Suspense fallback={<div className="min-h-screen" />}>
        <BookingSection />
      </Suspense>

      {/* Footer */}
      <Suspense fallback={<div className="min-h-[400px]" />}>
        <div id="contact">
          <Footer />
        </div>
      </Suspense>

      {/* Back to Top Button */}
      <Suspense fallback={null}>
        <BackToTop />
      </Suspense>
    </main>
    </>
  )
}
